import Vue from 'vue';
import VueRouter from 'vue-router';
import Board from '../views/Board.vue';
//import Index from '@/components/board/index.vue';
import List from '@/components/board/list.vue';
import Create from '@/components/board/create.vue';
import Read from '@/components/board/read.vue';
import Update from '@/components/board/update.vue';
import Delete from '@/components/board/delete.vue';

Vue.use(VueRouter);
export const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'list',
      component: List,
    },
    {
      path: '/board.html',
      name: 'list',
      component: List,
    },
    {
      path: '/board',
      name: 'Board',
      component: Board,
      children: [
        {
          path: '',
          name: 'list',
          component: List,
        },
        {
          path: 'create',
          name: 'create',
          component: Create,
        },
        {
          path: 'read/:no',
          name: 'read',
          component: Read,
        },
        {
          path: 'update/:no',
          name: 'update',
          component: Update,
        },
        {
          path: 'delete/:no',
          name: 'delete',
          component: Delete,
        },
      ]
    },
  ],
});
