<template>
  <!-- <tr>
    <td>{{ no }}</td>
    <td>
      <router-link :to="`board/read/${no}`">{{ title }}</router-link>
    </td>
    <td>{{ writer }}</td>
    <td>{{ getFormatDate(regtime) }}</td>
  </tr> -->
  <div class="row border-1 p-2">
    <div class="col-1 text-left">{{ no }}</div>
    <div class="col-5">{{ title }}</div>
    <div class="col-2">
      <strong class="text-danger">{{ writer }}</strong>
    </div>
    <div class="col-2">
      <span class="badge badge-info">{{ getFormatDate(regtime) }}</span>
    </div>
    <div class="col-2">
      <router-link
        :to="`board/read/${no}`"
        class="btn btn-primary"
        title="Edit Product"
        >
        <i class="fa fa-pencil"></i></router-link
      >
      <router-link :to="`/board/delete/${no}`">
        <button class="btn btn-danger ml-2" title="Delete Product">
          <i class="fa fa-trash"></i>
        </button>
      </router-link>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
export default {
  name: 'row',
  props: {
    no: { type: Number },
    writer: { type: String },
    title: { type: String },
    regtime: { type: String },
    content: { type: String },
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD');
    },
  },
};
</script>
