<template>
  <div>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ item.no }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.writer }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.title }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.regtime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ item.content }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <router-link to="/board"
        ><button class="btn btn-primary" style="margin:5px">목록</button></router-link
      >
      <router-link :to="`/board/update/${item.no}`"
        ><button class="btn btn-warning" style="margin:5px">수정</button></router-link
      >
      <router-link :to="`/board/delete/${item.no}`">
        <button class="btn btn-danger" style="margin:5px">삭제</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import moment from 'moment';
export default {
  name: 'detail',
  // props: {
  //   no: { type: Number },
  //   writer: { type: String },
  //   title: { type: String },
  //   content: { type: String },
  //   regtime: { type: String },
  // },
  computed: {
    ...mapGetters(["item"])
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
