<template>
  <div class="container">
    <div class="card">
      <div class="card-header">
        <h5>글 {{this.type == 'create' ? "작성" : "수정"}}</h5>
      </div>
      <div class="card-body">
        <div class="form-group">
          <label for="writer">작성자</label>
          <input
            type="text"
            class="form-control"
            id="writer"
            ref="writer"
            placeholder="작성자를 입력하세요"
            v-model="writer"
          />
        </div>
        <div class="form-group">
          <label for="title">제목</label>
          <input
            type="text"
            class="form-control"
            id="title"
            ref="title"
            placeholder="제목을 입력하세요"
            v-model="title"
          />
        </div>
        <div class="form-group">
          <label for="content">내용</label>
          <textarea
            type="text"
            class="form-control"
            id="contnet"
            ref="content"
            placeholder="내용을 입력하세요"
            v-model="content"
            rows=5
          ></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-success" style="margin:5px" v-if="type == 'create'" @click="checkHandler">등록</button>
          <button class="btn btn-warning" style="margin:5px" v-else @click="checkHandler">수정</button>
          <button class="btn btn-primary" style="margin:5px" @click="moveList">목록</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import http from '@/util/http-common';
export default {
  name: 'board-Form',
  props: {
    type: { type: String },
  },
  data: function() {
    return {
      no: '',
      regtime: '',
      writer: '',
      title: '',
      content: '',
      //items: [],
    };
  },
  computed: {
    ...mapGetters(["items","item"]),
  },
  methods: { 
    checkHandler() {
      let err = true;
      let msg = '';
      !this.writer &&
        ((msg = '작성자를 입력해주세요'),
        (err = false),
        this.$refs.writer.focus());
      err &&
        !this.title &&
        ((msg = '제목 입력해주세요'), (err = false), this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = '내용 입력해주세요'),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      else this.type == 'create' ? this.createHandler() : this.updateHandler();
    },
    createHandler() {
      console.log(this.items);
      http
        .post('/board', {
          no: this.items.length + 1,
          writer: this.writer,
          title: this.title,
          content: this.content,
          regtime: this.regtime,
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('등록 처리시 에러가 발생했습니다.');
        });
    },
    updateHandler() {
      http
        .put(`/board/${this.no}`, {
          no: this.no,
          regtime: this.regtime,
          writer: this.writer,
          title: this.title,
          content: this.content,
        })
        .then(({ data }) => {
          let msg = '수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '수정이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('수정 처리시 에러가 발생했습니다.');
        });
    },
    moveList() {
      this.$router.push('/board');
    },
  },
  created() {
    if (this.type === 'update') {
      // http
      //   .get(`/board/${this.$route.query.no}`)
      //   .then(({ data }) => {
      //     this.no = data.no;
      //     this.regtime = data.regtime;
      //     this.writer = data.writer;
      //     this.title = data.title;
      //     this.content = data.content;
      //   })
      //   .catch(() => {
      //     alert('에러가 발생했습니다.');
      //   });
      this.$store.dispatch("getItem", this.$route.params.no);
      this.no = this.item.no;
      this.regtime = this.item.regtime;
      this.writer = this.item.writer;
      this.title = this.item.title;
      this.content = this.item.content;
    }
    else{
      this.$store.dispatch("getItems");
    }
  },
};
</script>

<style scoped></style>
