import Vue from 'vue';
import Vuex from 'vuex';
import http from "@/util/http-common";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    items: [],
    item: {},
  },
  getters: {
    items(state) {
        return state.items;
    },
    item(state) {
        return state.item;
    },
  },
  mutations: {
    setItems(state, payload) {
        state.items = payload;
    },
    setItem(state, payload) {
        state.item = payload;
    },
  },
  actions: {
    getItems(context) {
      http.get("/board").then(({data}) => {
          context.commit("setItems", data);
      }).catch(() => {
          alert("게시판 목록: 에러발생!");
      });
    },
    getItem(context, payload) {
      http.get("/board/"+payload).then(({data}) => {
          context.commit("setItem", data);
      }).catch(() => {
          alert("게시판 조회: 에러발생!");
      });
    },
  },
})
